.cbx {
  margin: auto;
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
  span {
    display: inline-block;
    vertical-align: middle;
    transform: translate3d(0,0,0);
    &:first-child {
      position: relative;
      width: 18px;
      height: 18px;
      border-radius: 3px;
      transform: scale(1);
      vertical-align: middle;
      border: 1px solid var(--color-divider);
      transition: all .2s ease;
      svg {
        position: absolute;
        top: 3px;
        left: 2px;
        fill: none;
        stroke: #FFFFFF;
        stroke-width: 2;
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-dasharray: 16px;
        stroke-dashoffset: 16px;
        transition: all .3s ease;
        transition-delay: .1s;
        transform: translate3d(0,0,0);
      }
      &:before {
        content: "";
        width: 100%;
        height: 100%;
        background: var(--color-inactive);
        display: block;
        transform: scale(0);
        opacity: 1;
        border-radius: 50%;
      }
    }
  }
  &:hover span:first-child {
    border-color: var(--color-inactive);
  }
}
.inpCbx:checked + .cbx {
  span {
    &:first-child {
      background: var(--color-inactive);
      border-color: var(--color-inactive);
      animation: wave .4s ease;
      svg {
        stroke-dashoffset: 0;
      }
      &:before {
        transform: scale(3.5);
        opacity: 0;
        transition: all .6s ease;
      }
    }
  }
}
@keyframes wave {
  50% {
    transform: scale(.9);
  }
};